}

} */

.book-card-body {
	margin: auto;
	background: #1f4963 !important;
	width: 60vw;
	align-items: center;
}

.book-card-image {
	width: 30vw !important;
	padding-top: 20px;
}

.book-info {
	color: #fde9c9;
}

.review-text-visible {
	display: block !important;
}

.review-text-invisible {
	display: none !important;
}

@media screen and (min-width: 1024px) {
	.book-card-body {
		width: 30vw !important;
		
	}

	.book-card-image {
		width: 15vw !important;
	}
}


.button {
	width: 100%;
	background-color: #fee9c9;
	color: #02897f;
}

.contact {
	width: 90%;
	margin: auto;
	text-align: left;
}

.mb-3 {
	color: #fee9c9;
}


@media (min-width: 1024px) {
	.contact {
		width: 50%;

		text-align: left;
	}
}

