.show-cards,
.show-books {
	margin: auto;
	display: flex;
	flex-wrap: wrap;
	align-items: space-between;
	justify-content: space-between;
}

.card {
	width: 50vw;
	margin-bottom: 5%;
	background: #1f4963 !important;
	align-items: center;

	color: #fee9c9 !important;
	padding-right: 10px;
	text-decoration: none;
}

.card-image {
	object-fit: cover;
	height: 40vh;
}

.add-button {
	bottom: 5px;
	padding-top: auto;
	padding-bottom: 10px;
}

@media screen and (min-width: 1024px) {
	.card {
		width: 18vw;
	}
}

